import React from 'react';
import { nop } from './utils';

var Category = React.createClass({
  getDefaultProps() {
    return {
      title: '無以名狀',
      image: 'http://placekitten.com/g/480/170'
    };
  },
  render() {
    var { className, children, title, image } = this.props;
    return (
      <div className={`yfs-comp category ${className || ''}`}>
        <h2 className="title">{title}</h2>
        <a className="image"
           href="#"
           onClick={nop}
           style={{
             'background-image': `url(${image})`
           }}
        ></a>
      </div>
    );
  }
});

export default Category;




