import React from 'react';

var Menu = React.createClass({
  getDefaultProps() {
    return { title: undefined };
  },
  // It will not travase into the nested structure, so it doesn't use
  // React.Children.*.
  _composeChild(child, i) {
    return child ?
      <li key={i}>{child}</li> :
      null;
  },
  // XXX: add a chevron down here may not be a good idea
  render() {
    var { className, children, title } = this.props;
    return (
      <nav className={`yfs-comp menu ${className || ''}`}>
        <span className="title">{title}</span>
        <i className="indicator fa fa-chevron-down"></i>
        <ul className="items">{children.map(this._composeChild)}</ul>
      </nav>
    );
  }
});

export default Menu;




