import React from 'react';
import { nop } from './utils';

var Slide = React.createClass({
  getDefaultProps() {
    return {
      current: 0,
      images: [
        'http://placekitten.com/g/980/280',
        'http://placekitten.com/g/980/280',
        'http://placekitten.com/g/980/280'
      ]
    };
  },
  _renderImage(url) {
    return (
      <li>
        <a className="image"
           href="#"
           onClick={nop}
           style={{
             'background-image': `url(${url})`
           }}
        ></a>
      </li>
    );
  },
  _renderIndicator(url, i) {
    var { current } = this.props;
    return <li className={`indicator ${i === current ? 'actived' : ''}`}></li>;
  },
  render() {
    var { className, children, images } = this.props;
    return (
      <div className={`yfs-comp slide ${className || ''}`}>
        <ol className="images">
          {images.map(this._renderImage)}
        </ol>
        <ol className="indicators">
          {images.map(this._renderIndicator)}
        </ol>
        {children}
      </div>
    );
  }
});

export default Slide;




