import React from 'react';
import { PureRenderMixin } from 'react/addons';
import CategoryList from './CategoryList';
import CategoryStore from '../../stores/CategoryStore';
import ListenerMixin from 'alt/mixins/ListenerMixin';

function treeFromCategory(indexes, categories, cat) {
  return {
    id: cat.id,
    title: cat.title,
    children: cat.children.map(id => {
      var idx = indexes[id];
      return idx !== undefined ?
        treeFromCategory(indexes, categories, categories[idx]) : // a category
        id ;                                                     // a product
    })
  }
}

var CategoryListContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    var { indexes, categories } = CategoryStore.getState(),
        menu = categories[categories.length - 1]; //last one
    return { categories: treeFromCategory(indexes, categories, menu).children };
  },
  componentDidMount() {
    this.listenTo(CategoryStore, this._onChange);
  },
  _onChange() {
    this.setState(this.getInitialState());
  },
  render() {
    console.log(this.state);
    return <CategoryList {...this.state} />;
  }
});

export default CategoryListContainer;




