import React from 'react';
import { Link } from 'react-router-component';
import ProductActions from '../../actions/ProductActions';

var SearchBar = React.createClass({
  getInitialState() {
    return {
      term: ''
    };
  },
  _onTermChange(e) {
    this.setState({ term: e.target.value });
  },
  _onKeyUp(e) {
    if (e.keyCode === 13) {
      this.refs.submit.getDOMNode().click();
    }
  },
  _onSearch(e) {
    var { term } = this.state;
    ProductActions.search(term);
  },
  render() {
    var { children } = this.props;
    var { term } = this.state;
    return (
      <div className="yfs-comp search-bar">
        <div className="container">
          <select className="points-range">
            <option>-請選擇點數範圍-</option>
            <option>0~5,000點</option>
            <option>5,001~10,000點</option>
            <option>10,001~20,000點</option>
            <option>20,001~30,000點</option>
            <option>30,001~50,000點</option>
            <option>50,001~100,000點</option>
            <option>100,001~500,000點</option>
            <option>500,000點以上</option>
          </select>
          <input className="term"
                 type="text"
                 placeholder="商品名、商品編號"
                 value={term}
                 onChange={this._onTermChange}
                 onKeyUp={this._onKeyUp} />
          <Link globalHash
                ref="submit"
                className="submit"
                href="/search"
                onClick={this._onSearch}>
            <i className="fa fa-search fa-lg"></i>
          </Link>
          { children }
        </div>
      </div>
    );
  }
});

export default SearchBar;




