import React from 'react';

var Pagination = React.createClass({
  getDefaultProps() {
    return {
      total: 10,
      visible: 6,
      current: 0,
      onPageChange: e => {}
    };
  },
  _prev(e) {
    var { current, onPageChange } = this.props;
    e.preventDefault();
    if (current > 0) {
      onPageChange(current - 1);
    }
  },
  _next(e) {
    var { current, total, onPageChange } = this.props;
    e.preventDefault();
    if (current < total - 1) {
      onPageChange(current + 1);
    }
  },
  _renderButtons() {
    var { total, visible, current, onPageChange } = this.props,
        ret = [],
        bgn, end;
    bgn = total   <           visible ?                 0 :
          current <                 2 ?                 0 :
          current >=  total - visible ?   total - visible :
                                              current - 2 ;
    end =   total <           visible ?         total - 2 :
                                        bgn + visible - 2 ;
    if (end < bgn) end = bgn;
    for (let i = bgn; i < end; ++i) {
      let click = (i => e => {
        e.preventDefault();
        onPageChange(i);
      })(i);
      ret.push(
        <li key={i}>
          <a className={i === current ? 'actived' : ''}
             href="#"
             onClick={click}>{i+1}</a>
        </li>
      );
    }
    if (total === 0 || total === 1) {
      // do nothing
    } else if (bgn === total - visible || total < visible) {
      ret.push(
        <li key={end}>
          <a className={end === current ? 'actived' : ''}
             href="#"
             onClick={e => { e.preventDefault(); onPageChange(total-2); }}>
            {total-1}
          </a>
        </li>
      );
    } else {
      ret.push(<li key={end}><a className="disabled">…</a></li>);
    }
    if (total !== 0) {
      ret.push(
        <li key={end+1}>
          <a className={end+1 === current ? 'actived' : ''}
             href="#"
             onClick={e => { e.preventDefault(); onPageChange(total-1)}}>
            {total}
          </a>
        </li>
      );
    }
    return ret;
  },
  render() {
    return (
      <ul className="yfs-comp pagination">
        <li><a href="#" onClick={this._prev}>«</a></li>
        {this._renderButtons()}
        <li><a href="#" onClick={this._next}>»</a></li>
      </ul>
    );
  }
});

export default Pagination;




