import React from 'react';
import Product from './Product';
import Pagination from './Pagination';

const perPage = 12;

var SearchResult = React.createClass({
  getDefaultProps() {
    return {
      products: []
    };
  },
  getInitialState() {
    return {
      page: 0
    };
  },
  _renderProduct(product, i) {
    return <Product key={i} {...product} />;
  },
  render() {
    var { products } = this.props,
        { page } = this.state,
        last = Math.ceil(products.length / perPage);
    page = page >= last ? last - 1 : page;
    var bgn = page * perPage,
        end = bgn + perPage,
        visible = products.slice(bgn, end);
    return (
      <div className="yfs-comp search-result small">
        <div className="warning">本網站商品兌換活動有效期限至2015年12月31日止</div>
        {visible.map(this._renderProduct)}
        <hr />
        <div className="container">
          <Pagination total={last}
                      current={page}
                      onPageChange={page => this.setState({ page })}/>
        </div>
      </div>
    );
  }
});

export default SearchResult;




