import React from 'react';
import Product from './Product';

var ProductList = React.createClass({
  getDefaultProps() {
    return {
      products: []
    };
  },
  _renderProduct(product, i) {
    return <Product key={i} {...product}/>;
  },
  render() {
    var { products, children } = this.props;
    return (
      <div className="yfs-comp product-list small">
        {products.map(this._renderProduct)}
        {children}
      </div>
    );
  }
});

export default ProductList;




