import alt from '../alt';
import uuid from 'node-uuid';
import { zh } from 'lesstext';
import ProductActions from '../actions/ProductActions';

function createProducts(count = 20) {
  var indexes = {},
      products = [];
  for (let i = 0; i < count; ++i) {
    let id = uuid.v4();
    indexes[id] = products.length;
    products.push({
      id: id,
      title: zh.loremWords(12),
      image: 'http://placekitten.com/g/150/150',
      points: 888,
      dollars: 888,
      mixed: {
        points: 88,
        dollars: 88
      }
    });
  }
  return { indexes, products };
}

var matchAll = /.*/;


class ProductStore {
  constructor() {
    // for action binding
    this.bindActions(ProductActions);

    var { indexes, products } = createProducts(300);

    this.indexes    = indexes;
    this.products   = products;
    this.searchTerm = matchAll;
  }
  onPushProduct(p) {
    this.products.push(p);
  }
  onSearch(term) {
    console.log(term);
    this.searchTerm = term === '' ? matchAll : new RegExp(term);
  }
}

export default alt.createStore(ProductStore);




