import React from 'react';
import SearchBar from './SearchBar';
import AccountMenu from './AccountMenu';
import MenuBar from './MenuBar';
import CategoryListContainer from './CategoryListContainer';
import Message from './Message';
import Marquee from './Marquee';

var Header = React.createClass({
  render() {
    var { children } = this.props;
    return (
      <div className="yfs-comp header">
        <AccountMenu />
        <div id="yf-logo"></div>
        <h1 className="visually-hidden">
          <span className="zh">永豐銀行</span>
          <span className="en">Bank SinoPac</span>
        </h1>
        <div id="reward-logo"></div>
        <h2 className="visually-hidden">
          <span className="zh">豐市集紅利網</span>
          <span className="en">SinoPac Reward</span>
        </h2>
        <SearchBar />
        <MenuBar>
          <CategoryListContainer />
          <Marquee>
            重要通知！本網站紅利商品兌換僅限使用永豐餘...
          </Marquee>
          <Message>
            ↗煞氣a限時搶購↗．LINE×金剛狼．季節限定紅利兌換
          </Message>
        </MenuBar>
        {children}
      </div>
    );
  }
});

export default Header;




